import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import Channelcard from './Channelcard';
import Video from './Video';
import { Box} from '@mui/material';
import { Fetchfromapi } from './Fetchfromapi';
const Channeldetail = () => {
  const [channelDetail,setchanneldetail]=useState(null);
  const[videos,setvideos]=useState([]);
  const {id}=useParams();
//  consoling to see the channelDetaill and videoid  are coming or not
 console.log(videos,channelDetail);
useEffect(()=>{
Fetchfromapi(`channels?part=snippet&id=${id}`)
 .then((data)=> setchanneldetail(data?.items[0]));
  Fetchfromapi(`search?channelId=${id}&part=snippet&order=date`)
  .then((data)=> setvideos(data?.items ||[]));// setting an empty array
},[id])
//Consoling to see the api is fetching id or not 
//console.log(id);
 return (
   <Box minHeight='95vh'>
  <Box>
    <div style={{background: 'linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)',
    zIndex:10,
    height:'300px'
}}/>
<Channelcard channelDetail={channelDetail} marginTop='-110px'/>
  </Box>
    <Box>
    <Box sx={{mr:{sm : '100px'}}}/>
    <Video videos={videos}/>
    </Box>
 </Box>
  )
}

export default Channeldetail
